import "purecss/build/pure.css";
import * as React from "react";
import { useState } from "react";
import "../styles.scss";

import { Button, Container, Grid, IconButton, Link as MuiLink, Stack, Typography } from "@mui/material";
import API from "../utils/API";

import { getLocaleText, I18nText } from "../utils/I18n";

const example_training_results = {
    "userId": 1,
    "results": [
        {
            "wordId": 1,
            "correct": true
        },
        {
            "wordId": 2,
            "correct": true
        },
        {
            "wordId": 3,
            "correct": true
        },
        {
            "wordId": 4,
            "correct": false
        },
        {
            "wordId": 5,
            "correct": false
        }
        // ...
    ]
};

export default function Home(props: { lang: keyof I18nText }) {
    const { lang } = props;

    const [messageSent, setMessageSent] = useState<boolean>(false);

    const sendMessage = async () => {
        try {
            // 
            await API.post('/api/training/results', { message: example_training_results }); //  axios 
            setMessageSent(true); //  true
        } catch (error) {
            console.error(":", error);
        }
    };

    return (
        <Container maxWidth="md">
            <Typography variant="h5">
                {getLocaleText(
                    {
                        "en": " Studling",
                        "zh-Hant": " Studling",
                        "zh-Hans": " Studling",
                        "tto-bro": "EeRZ T8eHXQea",
                        "tto": "hFCmo mAFKRHm",
                        "ja": " Studling",
                        "de": " Studling",
                    },
                    lang
                )}
            </Typography>
            <Button
                variant="contained"
                color="primary"
                onClick={sendMessage}
                disabled={messageSent} // 
            >
                
            </Button>
        </Container>
    );
}
